<template>
  <div class="category">
    <b-button class="categorybt" variant="outline-success" size="md" @click="showModal(index)" v-for="(item,index) in list" ref="header">{{item.catename}}
    </b-button>

    <b-modal   ref="myModalRef" hide-footer title="ENJOY YOUR COFFEE" class="modal-dialog">
      <div class="d-block text-center">
        
        <h3 v-if="list[index].catename!=' Smoothies' && list[index].drinks[0]!=''">{{list[index].drinks[0]}} {{tag[0]}}</h3> 
        <h3 v-else>{{list[index].drinks[0]}}</h3>
        
      </div>
      <b-btn class="mt-3" variant="outline-danger" block @click="hideModal">Close Me</b-btn>
    </b-modal>

  <br>
  <br>
  <br>
   <b-alert show variant="light">
    Tips:.
  </b-alert>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tag: ["()", "()"],
      index:0,
      list: [
        {
          catename: " Classic Coffee",
          drinks: ["", "", "", "", ""]
        },
        {
          catename: " Specialty Coffee",
          drinks: [
            "",
            "",
            "",
            "",
            "",
            "",
            ""
          ]
        },
        {
          catename: " Smoothies",
          drinks: [
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            ""
          ]
        },
        {
          catename: " Tea",
          drinks: [
            "",
            "",
            "",
            "",
            "",
            "",
            ""
          ]
        }
      ],
      category: ["", "", "", ""]
    };
  },
  // computed: {
  //   reversedMessage: function() {
  //     // `this`  vm 
  //     function randomsort(a, b) {
  //       return Math.random() > 0.5 ? -1 : 1;
  //     }
  //     return this.ccoffee.sort(randomsort);
  //   }
  // },
  methods: {
    showModal(index) {
      this.index= index
      function randomsort(a, b) {
        return Math.random() > 0.5 ? -1 : 1;
      }
      this.list[index].drinks.sort(randomsort);
      this.tag.sort(randomsort);
      this.$refs.myModalRef.show();
    },
    hideModal() {
      this.$refs.myModalRef.hide();
    }
  }
};
</script>

<style lang="scss">
.category {
  text-align: center;
  .categorybt {
    padding: 0.5rem;
    width: 15rem;
    margin-top: 1rem;
  }
}
.modal-dialog {
  position: absolute;
  width: auto;
  margin: 50% 15% 15%;
}
</style>

